.points {
  &__wrapper {
    padding: 20px 25px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }

  &__menu {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    color: $color-default-white;
    font-size: 15px;
    font-weight: 400;
    line-height: 16px;
    text-align: left;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 15px;
  }

  &__item:not(:last-child) {
    margin-bottom: 10px;
  }

  &__link {
    font-family: "PT Mono", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    color: $color-default-white;
    opacity: 0.7;
    text-align: left;
  }
}

@media (min-width: $tablet-width) {
  .points {
    &__wrapper {
      border-bottom: none;
    }

    &__title {
      margin-bottom: 20px;
    }

    &__button {
      display: none;
    }
  }
}
